.main-title {
    /* font-family: 'Amaranth', sans-serif; */
    font-family: var(--title-font);
    font-size: 4.8em;
}

.home-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/700x500');
    /* background-repeat: no-repeat; */
    background-size: cover;
    /* background-position: top; */
    /* background-image: url('https://source.unsplash.com/2M_sDJ_agvs/1000x800'); */
}

.search-container {
    display: flex;
    align-items: center;
}

.search-box-container {
    margin: 1.5em;
}

.home-search-box {
    height: 2.5em;
}

.search-button {
    padding: 0.5em 0.7em;
    /* margin: 1em; */
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: var(--dark-button-color);
    border: none;
    cursor: pointer;
}

.buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-button {
    padding: 1.5em 1.2em;
    margin: 1em;
    border-radius: 0.5em;
    background-color: var(--light-button-color);
    font-size: 1.4em;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.nav-button:hover {
    background-color: var(--dark-button-color);
    color: white;
}

@media(min-width: 768px) {
    .main-title {
        font-size: 6em;
    }

    .search-container {
        margin-bottom: 1em;
    }

    .home-search-box {
        height: 3em;
    }

    .search-button {
        font-size: 1.6em;
        padding: 0.7em 1em;
    }

    .nav-button {
        padding: 1.5em;
        font-size: 1.5em;
    }
}

@media(min-width: 1200px) {

}
.map-container {
    width: 85%;
    height: 300px;
    margin: auto;
}

.current-location-button {
    background-color: var(--dark-button-color);
    font-size: 1.2rem;
    padding: .5em 1em;
    border-radius: .5em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .5em;
    text-decoration: none;
}

.filter-map-buttons {
    text-align: center;
    margin: 0 0 0.5rem 0;
}

.filter-button {
    font-size: 1.2rem;
    padding: .5em 1em;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .5em;
    text-decoration: none;
}

.all-filter {
    background-color: rgb(25, 125, 255);
}

.not-found-filter {
    background-color: rgb(16, 168, 16);
}

.found-filter {
    background-color: rgb(253, 60, 60);
}

.search-input-container {
    display: flex;
    justify-content: space-evenly;
    padding: 1rem 0 0 0.8rem
}

.search-box {
    box-sizing: border-box;
    width: 20rem;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    font-size: 12px;
    outline: none;
    text-overflow: ellipses;
    padding: 2px 10px;
}

.info-window-container {
    display: flex;
    align-items: center;
    margin: .5em;
}
.info-window-image {
    width: 50px;
    height: 50px;
}

.info-window-section {
    margin: .5em;
}

.info-window-title {
    font-family: var(--main-text-font);
    font-weight: bold;
    margin: 0;
    font-size: 1.2rem;
}

.info-window-status {
    font-family: var(--main-text-font);
    font-size: 1rem;
}

.info-window-link {
    font-family: var(--main-text-font);
    font-size: 1.2rem;
    margin: 0;
}

@media(min-width: 360px) {
    .info-window-image {
        width: 70px;
        height: 70px;
    }
    .info-window-title, .info-window-link {
        font-size: 1.5rem;
    }
    .info-window-status {
        font-size: 1.2rem;
    }
}

@media(min-width: 768px) {
    .search-map-screen {
        display: flex;
        flex-wrap: wrap;
    }
    .search-box {
        width: 33rem;
        font-size: 1.4rem;
    }
    .current-location-button {
        font-size: 1.6rem;
    }
    .filter-map-btns-container {
        display: inline-block;
        order: 1;
        width: 25%;
        margin: auto;
    }
    .filter-map-buttons {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .filter-button {
        font-size: 1.6rem;
        width: 50%;
    }
    .map-container {
        display: inline-block;
        width: 60%;
        margin: 0 auto;
    }
    .form-btns-container {
        text-align: center;
        width: 100%;
        order: 2;
    }
}

@media(min-width: 1200px) {
    .search-box {
        width: 45rem;
    }
}
.form-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.success-image-background {
    background-image: url('https://source.unsplash.com/DbwYNr8RPbg/1200x1000');
    /* background-repeat: no-repeat; */
    background-size: cover;
    background-position: center;
}

.form-title {
    margin: .5em 0 0 0;
    font-size: 3rem;
    font-family: 'Cabin Sketch', cursive;
    text-align: center;
}

.form-container {
    display: flex;
}

.add-item-form {
    width: 30em;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: 1.5em auto;
    align-items: center;
    border: 2px solid var(--dark-button-color);
    background-color: var(--light-button-color);
    border-radius: 1em;
    overflow-y: auto;
}

.form-section-title {
    font-family: var(--title-font);
    font-weight: bold;
    font-size: 1.8rem;
    text-align: center;
    margin: .5em;
}

.form-text { 
    font-family: var(--main-text-font);
    font-size: 1.4rem;
    text-align: center;
    margin: .5em;
    letter-spacing: .05rem;
    line-height: 1.5;
}

.id-box {
    height: 4rem;
}

.id-text {
    margin: 0;
    font-size: 2rem;
    text-align: center;
    padding: .5rem;
}

.form-submit-button {
    background-color: var(--dark-button-color);
    font-size: 1.7rem;
    padding: .5em;
    width: 10rem;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .8em;
}

.form-button {
    background-color: var(--dark-button-color);
    font-size: 1.2rem;
    padding: .5em 1em;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .5em;
    text-decoration: none;
}

.input-box {
    background-color: white;
    box-sizing: border-box;
    width: 200px;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(255, 149, 11, 0.3);
    font-size: 12px;
    outline: none;
    /* text-overflow: ellipses; */
    padding: 2px 10px;

}

.form-section {
    display: flex;
    flex-direction: column;
    width: 75%;
    align-items: center;
    margin: 1em auto
}

.description-box {
    height: 70px;
}

.form-btns-container {
    text-align: center;
}

.new-hint-container {
    display: flex;
    align-items: center;
    height: 2.5em;
}

.fill-height {
    height: 100%;
}

.hint-listings {
    width: 80%;
    margin: 1.5em;
    border: 1px solid black;
    border-radius: .5em;
}

.hint-listing {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid black;
}

.hint-listing-text {
    padding-left: 1em;
    font-size: 1.2rem;
}

.no-hints-message {
    font-family: var(--main-text-font);
    font-size: 1.5rem;
    font-weight: bold;
    margin: 0;
    padding-top: 1em;
}

.found-comment-thank-you {
    font-size: 1.5rem;
    font-family: var(--main-text-font);
    font-weight: bold;
    width: 60%;
    margin: auto;
    padding: 1em 0;
    text-align: center;

}

@media(min-width: 768px) {
    .add-item-form {
        width: 500px;
        border: 4px solid var(--dark-button-color);
    }
    .form-title {
        font-size: 4rem;
    }
    .form-section-title {
        font-size: 2.5rem;
    }
    .input-box {
        font-size: 2rem;
        width: 90%;
    }
    .form-button {
        font-size: 2rem;
    }
    .description-box {
        height: 100px;
    }
    .form-text {
        font-size: 1.5rem;
    }
}
.map-detail-container, .image-container, .hints-container {
    border: 1px solid black;
}

.center-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

hr {
    margin: 0 auto;
    width: 80%;
    border: .5px solid var(--dark-button-color);
}

.item-details-container {
    height: 90%;
    width: 30em;
    border: 2px solid var(--dark-button-color);
    background-color: var(--light-button-color);
    border-radius: 1em;
}

.item-detail-form {
    height: 100%;
    width: 100%;
}

.item-detail-form-header {
    height: 15%;
}

.item-detail-title {
    margin: 0;
    font-family: var(--title-font);
    font-size: 2rem;
}
.item-detail-form-body {
    height: 70%;
    overflow-y: scroll;
}

.item-detail-body-section {
    width: 90%;
    margin: 1em auto;
}

.item-detail-map-container {
    width: 20em;
    height: 20em;
    margin: auto;
    border: 2px solid var(--dark-button-color);
}

.item-detail-image-container {
    width: 45%;
    padding-top: 45%;
    border: 2px solid var(--dark-button-color);
}

.item-details-section-title {
    margin: 0;
    font-family: var(--title-font);
    font-size: 2rem;
    text-align: center
}

.status-wrapper {
    display: flex;
    flex-wrap: wrap;
}

.item-detail-status-section {
    width: 55%;
}

.item-details-status-text {
    font-family: var(--main-text-font);
    font-size: 1.5rem;
    margin: 0;
    padding: .5em;
    text-align: center;
}

.status-comment-container {
    display: flex;
    justify-content: center;
    padding: 1em .5em;
}

.status-comment-font {
    font-family: var(--main-text-font);
    font-size: 1.2rem;
    margin: 0;
    text-align: center;
}

.hints-container {
    width: 85%;
    margin: .5em auto;
    border: 1px solid black;
    border-radius: .5em;
}

.item-detail-hint-listing {
    margin: 0;
    font-family: var(--main-text-font);
    font-size: 1.2rem;
    padding: .5em;
    text-align: center;
    border-bottom: 1px solid black;
}

.item-detail-form-footer {
    height: 15%;
}

.detail-screen-button {
    background-color: var(--dark-button-color);
    font-size: 1.2rem;
    width: 7em;
    height: 3em;
    margin: 0 1em;
    padding: .5em 1em;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    text-decoration: none;
}

.desktop-map-display {
    display: none;
}

@media(min-width: 768px) {
    .mobile-map-display {
        display: none;
    }
    .item-details-container {
        width: 90%;
        display: flex;
    }
    .item-detail-form {
        width: 45%;
    }
    .desktop-map-display {
        width: 55%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .item-detail-map-container {
        width: 90%;
        height: 80%;
    }
    .detail-screen-button {
        font-size: 1.5rem;

    }
}

@media(min-width: 1200px) {
    .item-details-container {
        width: 70%;
    }
}

@media(min-height: 768px) {
    .item-details-container {
        height: 500px;
    }
}

@media(min-height: 1200px) {
    .item-details-container {
        height: 700px;
    }
}


.search-items-button {
    padding: 0.5em 0.7em;
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: var(--dark-button-color);
    border: none;
    cursor: pointer;
    margin: auto;
}

.search-id-form-section {
    display: flex;
}

.search-id-box {
    height: 2.8rem;
    margin: 0.8rem;
}

@media(min-width: 768px) {
    .search-id-box {
        height: 3.7rem;
        width: 24rem;
        margin: 0.7rem 1.6rem;
    }
    .search-items-button {
        font-size: 1.6em;
        padding: 0.65em 1em;
    }
}
.not-found-image-background {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-direction: space-between;
    background-image: url('https://source.unsplash.com/2M_sDJ_agvs/4000x3000');
    background-size: cover;
    background-position: center;
}

.not-found-title {
    /* border: 0.3rem solid white;
    border-radius: 0.2em;
    padding: 1.5rem; */
    font-family: var(--title-font);
    font-size: 3.5em;
    color: black;
    margin: 0.5em 0;
}

.not-found-text {
    font-family: 'Neuton', serif;
    font-size: 2.3em;
    margin: 0 0.6em;
}

.little-dots {
    font-size: 3em;
    color: white;
}

.little-star {
    font-size: 0.8em;
}

